import React from 'react';
import connect from '@vkontakte/vkui-connect';
import { View } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';
import Persik from './panels/Persik';

class App extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			activePanel: 'home',
			fetchedUser: null,
		};
	}

	componentDidMount() {
		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppGetUserInfoResult':
					this.setState({ fetchedUser: e.detail.data });
					break;
				default:
					console.log(e.detail.type);
			}
		});
		connect.send('VKWebAppGetUserInfo', {});
	}

	go = (e) => {
		this.setState({ activePanel: e.currentTarget.dataset.to })
	};
	
	randomInteger = (min, max) => {
	  let rand = min - 0.5 + Math.random() * (max - min + 1);
	  return Math.round(rand);
	};
	
	newrandnom = (e) => {
		//Получаем кол-во элмемментов по классу rigma
		var length = document.getElementsByClassName('rifma').length;
		//Получаем рандомное число от 1 до кол-во элементов с классом rifma
		var randomnumber = this.randomInteger(1,length);
		//Записываем рандомную рифму найденую по id+число рандомной рифмы по классу
		var node = document.getElementById('rifma'+randomnumber),
			textContent = node.innerHTML;
		//Получаем блок где написана случайная рифма	
		var rifmablock = document.getElementById('rifmablock');
		//Записываем случайную рифму в блок с рифмой
		rifmablock.innerHTML = textContent;

	};
	
	share = (e) => {
		console.log(e.currentTarget.id);
		var node = document.getElementById(e.currentTarget.id),
			textContent = node.textContent;
			
		connect.send("VKWebAppShowWallPostBox", {"message": textContent});
	};

	render() {
		return (
			<View activePanel={this.state.activePanel}>
				<Home id="home" 
					fetchedUser={this.state.fetchedUser} 
					go={this.go} 
					newrandnom={this.newrandnom}
					share={this.share}
				/>
				<Persik id="persik" go={this.go} />
			</View>
		);
	}
}

export default App;
